<template>
    <div class="main">
        <div class="payManagement">
            <div class="searchA">
                <el-select v-model="search.fee_type" clearable placeholder="请选择" size="small">
                    <el-option v-for="item in fee_type_list" :label="item.dictionary_name" :value="item.dictionary_value" :key="item.dictionary_value"></el-option>
                </el-select>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
                <div class="add">
                    <el-button size="small" type="primary" @click="addBtn()">新增缴费</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :min-width="item.minWidth"></el-table-column>
                    </template>
                    <el-table-column prop="address" align="center" label="操作" fixed="right" width="150">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="edit(scope.row)">编辑</el-button>
                            <el-button type="text" size="small" @click="check(scope.row)">查看</el-button>
                            <el-button type="text" size="small" style="color:#ff2b2b" @click="delBtn(scope.row.id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" v-dialogDrag :visible.sync="dialogVisible" width="600px" :close-on-click-modal="false">
                <el-form :model="formData" size="medium" ref="formData" :rules="rule" label-width="110px">
                    <el-form-item label="缴费类型" prop="fee_type">
                        <el-select v-model="formData.fee_type" style="width:100%" placeholder="请选择缴费类型">
                            <el-option v-for="item in fee_type_list" :label="item.dictionary_name" :value="item.dictionary_value" :key="item.dictionary_value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="应缴金额" prop="fee_total">
                        <el-input type="text" size="small" maxlength="7" @input="formData.fee_total=formatDecimal(formData.fee_total,2)" v-model.trim="formData.fee_total" placeholder="请输入缴费金额"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="submit">确 定</el-button>
                </div>
            </el-dialog>

            <!-- 新增编辑弹窗 -->
            <el-dialog title="查看" v-dialogDrag :visible.sync="dialogVisibleCheck" width="60%" :close-on-click-modal="false">
                <el-descriptions direction="vertical" :column="7" border>
                    <el-descriptions-item label="缴费类型">{{formInfo.fee_type_name}}</el-descriptions-item>
                    <el-descriptions-item label="应缴金额">{{formInfo.payable_fee}}</el-descriptions-item>
                    <el-descriptions-item label="预计缴费人数">{{formInfo.predict_count}}</el-descriptions-item>
                    <el-descriptions-item label="预计费用">{{formInfo.predict_fee}}</el-descriptions-item>
                    <el-descriptions-item label="已交人数">{{formInfo.pay_count}}</el-descriptions-item>
                    <el-descriptions-item label="已交费用">{{formInfo.pay_fee}}</el-descriptions-item>
                    <el-descriptions-item label="未交费用">{{formInfo.unpay_fee}}</el-descriptions-item>
                </el-descriptions>
                <div class="home_table" style="margin-top:10px">
                    <el-table :data="tableDataInfo" border element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" style="width: 100%">
                        <template v-for="(item, index) in tableHeads">
                            <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :min-width="item.minWidth"></el-table-column>
                        </template>
                    </el-table>
                    <div class="pagination">
                        <el-pagination background :current-page.sync="pageNumInfo" :page-sizes="[10, 20, 30, 50]" :page-size="pageSizeInfo" layout="total, sizes, prev, pager, next" :total="totalInfo" class="pagination" @size-change="handleSizeChangeInfo" @current-change="handleCurrentChangeInfo" :hide-on-single-page="totalInfo<10? true:false" />
                    </div>
                </div>
            </el-dialog>

        </div>
    </div>
</template>
<script>
import {
    payManagementList,
    payManagementInfo,
    payManagementView,
    payManagementStudentList,
    payManagementAdd,
    payManagementEdit,
    payManagementDel,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 15,
            totalInfo: 0,
            pageNumInfo: 1,
            pageSizeInfo: 10,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                fee_type: '',
                fee_total: '',
            },
            formInfo: {},
            method_auth: {},
            editId: '',
            title: '',
            search: {
                fee_type: '',
            },
            dialogVisible: false,
            dialogVisibleCheck: false,
            tableHead: [
                { title: 'ID', prop: 'id', minWidth: '100', },
                { title: '缴费类型', prop: 'fee_type_name', minWidth: '120', },
                { title: '应缴金额', prop: 'fee_total', minWidth: '100', },
                { title: '预计缴费人数', prop: 'predict_count', minWidth: '120', },
                { title: '预计费用', prop: 'predict_fee', minWidth: '150', },
                { title: '已交人数', prop: 'pay_count', minWidth: '150', },
                { title: '已交费用', prop: 'pay_fee', minWidth: '150', },
                { title: '未交费用', prop: 'unpay_fee', minWidth: '150', },
                { title: '创建日期', prop: 'create_time', minWidth: '150', },
            ],
            tableHeads: [
                { title: 'ID', prop: 'id', minWidth: '100', },
                { title: '学生姓名', prop: 'student_name', minWidth: '120', },
                { title: '应缴金额', prop: 'payable_fee', minWidth: '100', },
                { title: '缴费状态', prop: 'pay_status_name', minWidth: '120', },
                { title: '已缴费用', prop: 'pay_fee', minWidth: '150', },
            ],
            rule: {
                fee_total: [
                    { required: true, message: '请输入缴费金额', trigger: 'blur' },
                ],
                fee_type: [
                    { required: true, message: '请选择缴费类型', trigger: ['blur', 'change'] }
                ],
            },
            tableData: [],
            tableDataInfo: [],
            hash: '',
            fee_type_list: [],
        };
    },
    mounted() {
        this.getList();
        this.resView();
        this.getHash();
    },
    methods: {
        formatDecimal(str, num) {//str当前输入值，num是想要保留的小数位数
            str = String(str)
            if (!str.trim()) {
                return str
            }
            var len1 = str.substr(0, 1)
            var len2 = str.substr(1, 1)
            // 如果第一位是0，第二位不是点，就用数字把点替换掉
            if (str.length > 1 && len1 === 0 && len2 !== '.') {
                str = str.substr(1, 1)
            }
            // 第一位不能是.
            if (len1 === '.') {
                str = ''
            }
            // 限制只能输入一个小数点
            if (str.indexOf('.') !== -1) {
                var str_ = str.substr(str.indexOf('.') + 1)
                if (str_.indexOf('.') !== -1) {
                    str = str.substr(0, str.indexOf('.') + str_.indexOf('.') + 1)
                }
            }
            // 正则替换
            str = str.replace(/[^\d^.]+/g, '') // 保留数字和小数点
            if (num || num === 0) {
                var rest = str.split('.')[1]
                if (rest && rest.length > num) {
                    rest = rest.substr(0, num)
                    str = str.split('.')[0] + '.' + rest
                }
            }
            return str
        },

        serchList() {
            this.pageNum = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getList();
        },

        // 监听 pagesize 改变
        handleSizeChangeInfo(newSize) {
            this.pageNumInfo = 1;
            this.pageSizeInfo = newSize;
            this.studentList();
        },
        // 监听 pagenum 改变
        handleCurrentChangeInfo(newPage) {
            this.pageNumInfo = newPage;
            this.studentList();
        },
        async studentList() {
            let data = {
                curr: this.pageNumInfo,
                pageSize: this.pageSizeInfo,
                id: localStorage.getItem('StudentList_id')
            };
            const res = await this.axios.post(payManagementStudentList, data);
            if (res.data.code == 1) {
                this.tableDataInfo = res.data.data.data;
                this.totalInfo = res.data.data.total;
                this.formInfo = res.data.data.basic;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async resView() {
            const res = await this.axios.post(payManagementView);
            if (res.data.code == 1) {
                this.fee_type_list = res.data.data.fee_type_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                curr: this.pageNum,
                pageSize: this.pageSize,
                fee_type: this.search.fee_type,
            };
            const res = await this.axios.post(payManagementList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        addBtn() {
            this.title = '新增缴费';
            this.dialogVisible = true;
            this.resetForm();
        },
        edit(row) {
            this.title = '编辑缴费';
            this.dialogVisible = true;
            this.resetForm();
            this.$nextTick(() => {
                this.getInfo(row.id)
            })
        },
        check(row) {
            localStorage.setItem('StudentList_id', row.id)
            this.dialogVisibleCheck = true;
            this.$nextTick(() => {
                this.studentList();
            })
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'payManagement' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        //学生档案详情
        async getInfo(id) {
            const res = await this.axios.post(payManagementInfo, { id: id });
            if (res.data.code == 1) {
                res.data.data.fee_type = String(res.data.data.fee_type);
                this.formData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        resetForm() {
            this.formData = {
                fee_type: '',
                fee_total: '',
            };
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
        submit() {
            this.$refs['formData'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let data = {
                        fee_type: this.formData.fee_type,
                        fee_total: this.formData.fee_total,
                        hash: this.hash,
                    }
                    let api;
                    if (this.title == '新增缴费') {
                        api = payManagementAdd
                    } else {
                        api = payManagementEdit;
                        data.id = this.formData.id;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getList();
                    this.getHash();
                }
            })
        },
        delBtn(id) {
            this.$confirm('是否确定删除', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                        hash: this.hash,
                    };
                    const res = this.axios.post(payManagementDel, datas).then(res => {
                        if (res.data.code == 1) {
                            this.$notify.success({
                                showClose: true,
                                message: res.data.msg,
                                type: 'success'
                            });
                            if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                this.page -= 1;
                            }
                            this.getList();
                        } else {
                            this.$notify.error({
                                title: '失败',
                                message: res.data.msg
                            });
                        }
                    });
                    this.getHash();
                })
                .catch(err => {
                    // this.$notify.info({
                    //     title: '取消',
                    //     message: '取消操作'
                    // });
                });
        },
    }
};
</script>

<style >
.payManagement .el-table__row--level-1 {
    background: #fdfafa !important ;
}
</style>
<style lang="scss" scoped>
.payManagement {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }
    .btn {
        width: 250px;
    }
    /deep/ .el-dialog {
        margin-top: 5vh !important;
    }
}
</style>



